@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{margin:0;padding:0;box-sizing:border-box;height:100%}body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%;min-height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}body::-webkit-scrollbar{width:0.6em;background:#040406}body::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}body::-webkit-scrollbar-thumb{background-color:#0f47c5;border-radius:8px}#loader{width:100%;height:100%;position:fixed;left:0;bottom:0;z-index:99999;background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5))}#loader .message{position:relative;font-size:20px;top:50%;left:50%;transform:translateX(-10%);color:#fff}.loader-open{overflow:hidden}.hide{display:none}

